@import "general";
@import "media";
html {
  font-size: $baseFontSize;

  @include tablets {
    font-size: $baseFontSize - 1px;
  }
  @include phones {
    font-size: $baseFontSize - 2px;
  }
}

body {
  height:100%
}
body {
  font-family:'Alegreya Sans';
  font-style: normal;
  line-height: 1.20;
  position:relative;
}

ul li {
  list-style: none;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
}

h1,h2,h3,h4,h5 {
  margin-top: 0;
  font-weight: normal;
}

.table {
  border-collapse: collapse;
}

.img{
  display: block;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

 }

.container {
  width: 95%;
    margin: 0 auto;
    max-width: $containerWidth;
}


.section {
  min-height: 36rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reason__img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

